import React from "react";
import style from "./Enquiry.module.scss";

import { useForm } from "react-hook-form";

import EnquiryModal from "@components/EnquiryModal";

import { postContact } from "../../apis/general";

export default function Enquiry({ onClose }) {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({ mode: "onBlur" });

  function onSubmit(data) {
    postContact({ ...data, message: `${data.msg} FOR FINANCIAL LITERACY` })
      .then((resp) => {})
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    true && (
      <EnquiryModal onClose={onClose}>
        <div className={style.wrapper}>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input placeholder="Enter name of your child" {...register("child_name", { required: "* Required" })} />
            {errors.child_name && <p>{errors.child_name.message}</p>}

            <input
              placeholder="Email Address"
              {...register("email", {
                required: "* Required",
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: "invalid email address",
                },
              })}
            />
            {errors.email && <p>{errors.email.message}</p>}

            <input
              type="number"
              placeholder="Mobile Number"
              {...register("phone", {
                required: "* Required",
                maxLength: 11,
                minLength: 8,
              })}
            />
            {errors.phone && <p>Please enter valid phone number.</p>}

            <textarea {...register("message", { required: "* Required" })} placeholder="Message" />
            {errors.message && <p>{errors.message.message}</p>}

            <input type="submit" />
          </form>
        </div>
      </EnquiryModal>
    )
  );
}
